@import "https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Playfair Display, serif;
}

body {
  background: #bbc6fa;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100vh;
  display: flex;
}

#already-saved-div {
  background-color: #b20000;
  border-radius: 30px;
  transition: all .2s;
  animation-name: top-to-bottom;
  animation-duration: 2s;
  display: none;
  box-shadow: 0 54px 55px #00000040, 0 -12px 30px #0000001f, 0 4px 6px #0000001f, 0 12px 13px #0000002b, 0 -3px 5px #00000017;
}

#already-saved-div p {
  color: #fff;
  padding: 20px;
  font-size: 25px;
}

@keyframes top-to-bottom {
  0% {
    background-color: #ca9898;
    transform: translateY(-200px);
  }

  100% {
    transform: translateY(0);
  }
}

.quote-box {
  text-align: center;
  background: #fff;
  border-radius: 10px;
  width: 50%;
  padding: 40px;
  box-shadow: 0 54px 55px #00000040, 0 -12px 30px #0000001f, 0 4px 6px #0000001f, 0 12px 13px #0000002b, 0 -3px 5px #00000017;
}

.quote-box h2 {
  margin-bottom: 40px;
  font-size: 2rem;
  position: relative;
}

.quote-box h2:after {
  content: "";
  background: #177ce5;
  border-radius: 3px;
  width: 75px;
  height: 3px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}

.quote-box blockquote {
  min-height: 110px;
  font-size: 26px;
}

.quote-box blockquote:before, .quote-box blockquote:after {
  content: "\"";
}

.quote-box span {
  float: right;
  margin-top: 10px;
  display: block;
  position: relative;
}

.quote-box span:before {
  content: "";
  background: #177ce5;
  width: 20px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: -25px;
}

.quote-box div {
  justify-content: center;
  width: 100%;
  margin-top: 50px;
  display: flex;
}

.quote-box button {
  color: #fff;
  cursor: pointer;
  background: #177ce5;
  border: 1px solid #177ce5;
  border-radius: 35px;
  justify-content: center;
  align-items: center;
  margin: 5px;
  padding: 20px;
  display: flex;
}

#div2 {
  margin-top: 30px;
}

#div2 button {
  background-color: #333;
  border: #000;
  font-size: 20px;
}

#list-div {
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 10px;
  min-width: 50%;
  max-width: 50%;
  margin-top: 20px;
  padding: 20px;
  animation-name: bottom-to-top;
  animation-duration: 2s;
  display: none;
  box-shadow: 0 54px 55px #00000040, 0 -12px 30px #0000001f, 0 4px 6px #0000001f, 0 12px 13px #0000002b, 0 -3px 5px #00000017;
}

#list-div p {
  padding: 10px 0;
  font-size: 20px;
}

#list-div span {
  margin-right: 10px;
  font-weight: bold;
}

@keyframes bottom-to-top {
  0% {
    transform: translateY(200px);
  }

  100% {
    transform: translateY(0);
  }
}

#reset-div {
  justify-content: flex-end;
  display: flex;
}

#reset-div i {
  cursor: pointer;
  font-size: 30px;
}

@media screen and (width <= 1000px) {
  .quote-box h2 {
    font-size: 16px;
  }

  .quote-box blockquote, .quote-box span, .quote-box div button, #list-div p, #already-saved-div p {
    font-size: 12px;
  }

  #div2 button {
    font-size: 14px;
  }

  .quote-box span:before {
    width: 15px;
    padding: 0;
    left: -15px;
  }

  #reset-div i {
    font-size: 10px;
  }
}

@media screen and (width <= 600px) {
  .quote-box h2 {
    font-size: 12px;
  }

  .quote-box blockquote, .quote-box span, .quote-box div button, #list-div p, #already-saved-div p, #div2 button {
    padding: 5px 10px;
    font-size: 6px;
  }
}
